<template>
  <div id="app">
    <input v-model="text" />
    <button @click="fnSubmit">OK</button>
    <ul>
      <li v-for="item in list" :key="item">{{ item }}</li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "App",
  data: () => ({
    text: "",
    list: [],
  }),
  methods: {
    fnSubmit() {
      this.list.push(this.text);
      this.text = "";
    },
  },
};
</script>

